@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Poppins',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(93, 129, 129);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fill {
  height: 100vh;
  display: flex;
  align-items: center;
  /* background-color: black; */
  color:rgb(255, 255, 255);
}

.front{
  width: 10px;
  font-size: 100px;
}

.row-center{
  margin-top: 18rem;
}

.title{
  font-size: 3rem;
}

.name{
  color: rgb(51, 199, 199);
  font-family: 'Poppins', sans-serif;
}

.fade-out{
  opacity: 1;
}

h2 span{
  color:  rgb(51, 199, 199);
}

section{
  margin-bottom: 10rem;
}

a:hover{
  text-decoration: none;
}
.personal {
  padding: 1rem;
}

.description {
  text-align: center;
}

ul li {
  list-style-type: square;
}

.me {
  height: 20rem;
  align-items: flex-end;
  display: flex;
}

p {
  color: white;
  font-weight: 600;
  font-size: large;
}

.vid {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.vid:hover {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}

.description {
  color: white;
  font-weight: 400;
  font-size: large;
  padding: 1rem;
}

.tech {
}

.tech-description {
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  padding: 0;
  width: 70%;
  text-align: left;
  list-style-position: inside;
}


h2 {
  text-align: left;
}

p {
  text-align: left;
  font-weight: 230;
}

span {
  color: white;
}

ol li {
  font-size: medium;
  padding-bottom: 0.7rem;
  padding-top: 0.7rem;
  padding-left: 0.5rem;
  text-align: left;
  cursor: pointer;
}

ol {
  padding: 0;
  margin: 0;
  list-style-position: inside;
  list-style-type: none;
}

h5 {
  text-align: left;
}

.job-description {
  text-align: left;
  list-style-position: outside;
  padding: 0;
  margin-left: 15px;
}

.job-description li {
  list-style-type: disc;
  font-weight: 230;
  padding-bottom: 0.5rem;
}

h5 a {
  color: rgb(51, 199, 199);
  text-decoration: none;
}

.company-li:hover {
  background-color: #476980;
}


.active {
  border-bottom: 2px solid #b9e3ff;
  display: inline-block;
  line-height: 0.85;
}

li span{
    padding: 0.1rem;
}
.stick {
  /* position: -webkit-sticky; Safari */
  /* position: fixed; */
  top: 0;
}

nav li{
    float: left;
    color: thistle;
    margin: 0;
}

nav ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333333;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 16px;
    text-decoration: none;
  }
  
  li a:hover {
    background-color: #111111;
  }

