.stick {
  /* position: -webkit-sticky; Safari */
  /* position: fixed; */
  top: 0;
}

nav li{
    float: left;
    color: thistle;
    margin: 0;
}

nav ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333333;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 16px;
    text-decoration: none;
  }
  
  li a:hover {
    background-color: #111111;
  }
